import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';

class Questions extends Component{
    render(){
        return(
            <Fragment>
                <main />
            </Fragment>
        )
    }
}



const mapDispatchToProps = dispatch => ({
    
})

const mapStateToProps = (state) => {
    let obj = {};
    for(let i in state.interfaceReducer){
         if(state.interfaceReducer.hasOwnProperty(i)){
            if(Object.keys(state.interfaceReducer[i]).length){
                obj[i] = state.interfaceReducer[i] ? state.interfaceReducer[i] : {};
            }else{
                obj[i] = state.interfaceReducer[i]
            }
         }
    }
    return obj;
};

export default connect(mapStateToProps,mapDispatchToProps)(Questions);
  