import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import * as actions from '../actions/index';

class Footer extends Component{
    _selectNext = (is_next) => {
        this.props._handleSelectNext(is_next);
    }

    render(){
        return(
            <Fragment>
                <footer>
                    <div className={'footer__wrapper'}>
                        <button className={'prev'}>이전</button>
                        <button className={'next'} onClick={()=>{this._selectNext(this.props.is_next)}}>다음</button>
                    </div>
                </footer>
            </Fragment>
        )
    }
}

const mapDispatchToProps = dispatch => ({
    _handleSelectNext: (is_next) => { dispatch(actions.selectNext(is_next)) }
})

const mapStateToProps = (state) => {
    let obj = {};
    for(let i in state.interfaceReducer){
         if(state.interfaceReducer.hasOwnProperty(i)){
            if(Object.keys(state.interfaceReducer[i]).length){
                obj[i] = state.interfaceReducer[i] ? state.interfaceReducer[i] : {};
            }else{
                obj[i] = state.interfaceReducer[i]
            }
         }
    }
    return obj;
};

export default connect(mapStateToProps,mapDispatchToProps)(Footer);
  