const intialState = {
  is_next:false
};

const COPY = (obj) => {
  if (obj !== undefined && obj !== null) {
    return JSON.parse(JSON.stringify(obj));
  }

  return null;
}

export default (state = intialState, action) => { 
    switch (action.type) {
      case 'NEXT':
        var nextState = COPY(state);
        nextState.is_next = !action.is_next;
        return nextState;
      default:
        return state
    }
  }