import React, { Component, Fragment } from 'react';
import Piece from '../assets/images/piece.svg';
import MyPiece from '../assets/images/my_piece.svg';

class Navigation extends Component{
    render(){
        return(
            <Fragment>
                <nav>
                    <div className={'nav__wrapper'}>
                        <div className={'nav__wrapper__active'}>
                            <ul>
                                <li>
                                    <img src={Piece} className={"ico_piece_active"} alt={'학습지 만들기'} />   
                                    <span>학습지 만들기</span>                                
                                    <i>2단계</i>
                                </li>
                            </ul>
                        </div>
                        <div className={'nav__wrapper__inactive'}>
                            <div className={'nav__wrapper__inactive--title'}>
                                <i></i>
                                <strong>문제지 관리</strong>
                            </div>
                            <ul>
                                <li>
                                    <img src={MyPiece} className={"ico_mypiece_inactive"} alt={'내 학습지'} />
                                    <span>내 학습지</span>
                                </li>
                            </ul>
                        </div>                                
                    </div>
                </nav>
            </Fragment>
        )
    }
}


export default Navigation;
  